import { CST } from '../CST';

export class LoadingScene extends Phaser.Scene{
    constructor() {
        super({
            key: CST.SCENES.LOAD
        })
    }

    preload(){
        this.load.image("title_bg",'./assets/images/title_bg.jpg');
        this.add.tileSprite(0,0,this.game.renderer.width,this.game.renderer.height,"title_bg");
    }
}